import helper from '../../common/common.js';
export default {
	data() {
		return {
			isactive:0,
			user_id:'',
			name:'',
			myphoto:'',
			phone:'',
			qqnum:'',
			webchatnum:'',
			email:'',
			personalinfo:'',
			idcard:'',
			cardphoto:'',
			city:'',
			issubmit:true,
			isphoto:true,
		}
	},
	onLoad() {
	    uni.getSystemInfo({
	        success: (res)=> {
	            this.windowHeight = res.windowHeight;
	        }
	    });    
	    uni.onWindowResize((res) => {
	        if(res.size.windowHeight+200 < this.windowHeight){
	            this.issubmit = false
	        }else{
	            this.issubmit = true
	        }
	    })
	},
	methods: {
		callback(Error){
			 console.log("name",Error);
			this.$refs.uToast.show({
				title: Error,
				type: 'default',
				// url: '../tellogin/tellogin'
			})
			 
		},
		back(){
			uni.switchTab({
			    url: '/pages/my/my'
			});
		},
		select(index){
			this.isactive = index
		},
		intro(e){
			this.personalinfo = e.detail.value
		},
		namecont(e){
			this.name = e.detail.value
		},
		phonecont(e){
			this.phone = e.detail.value
		},
		qqnumcont(e){
			this.qqnum = e.detail.value
		},
		webchatnumcont(e){
			this.webchatnum = e.detail.value
		},
		emailcont(e){
			this.email = e.detail.value
		},
		citycont(e){
			this.city = e.detail.value
		},
		idcardcont(e){
			this.idcard = e.detail.value
		},
		photo(){
			var that = this;
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
					 uni.uploadFile({
						  url:helper.websiteUrl+"service_uploadimg",		//post请求的地址
						  filePath:res.tempFilePaths[0],
						  header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
						  name:'images',	
						  success: (uploadFileRes) => {
							  console.log(uploadFileRes)
							   var obj = JSON.parse(uploadFileRes.data);
							   console.log("1"+obj.data)
								that.isphoto = false;
								that.myphoto = helper.websiteUrl+obj.data;
						  }
					})
			        
			    }
			});
		},
		submit(){
			if(this.name == '' || this.isactive == 0 || this.phone == '' || this.myphoto == ''){
				  this.callback('请将必填写信息补全');
				  return
			}else{
				if (this.phone !== '') {
					  var reg=/^1[3456789]\d{9}$/;
					  if(!reg.test(this.phone)){
						this.callback('请输入有效的手机号码');
						return
					  }
						 
				}else{
					this.callback('手机号码不能为空');
					return
				}
			}
			
			uni.request({
				url:helper.websiteUrl+"designer_authen",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'user_id':this.user_id,
					'servicearea':this.isactive,
					'name':this.name,
					'phone':this.phone,
					'qqnum':this.qqnum,
					'webchatnum':this.webchatnum,
					'email':this.email,
					'personalinfo':this.personalinfo,
					'idcard':this.idcard,
				},
				success: (res) => {
					this.callback('提交成功');
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/my/my'
						});
					}, 1000);
				}
			});
		}
	}
}
